import { useState, useEffect } from 'react';
import { useLazyGetUserReposQuery, useSearchUsersQuery } from '../store/github/github.api';
import { useDebounce } from '../hooks/useDebounce';
import RepoCard from '../components/RepoCard';

function HomePage() {
  const [search, setSearch] = useState('');
  const [dropdown, setDropdown] = useState(false);
  const debounce = useDebounce(search, 500);
  const { isLoading, isError, data } = useSearchUsersQuery(debounce, { skip: debounce.length < 3 });
  const [fetchRepos, { isLoading: areReposLoading, data: repos }] = useLazyGetUserReposQuery();

  useEffect(() => {
    setDropdown(debounce.length > 3 && data?.length! > 0);
  }, [data, debounce]);

  const clickHandler = (username: string) => {
    setDropdown(false);
    fetchRepos(username);
  };

  return (
    <div className="flex justify-center pt-10 mx-auto h-screen">
      {isError && <p className="text-center text-red-600">Something went wrong...</p>}
      <div className="relative w-[560px] mx-auto">
        <input
          type="text"
          className="border py-2 px-4 w-full h-[42px] mb-2"
          placeholder="search for github username"
          value={search}
          onChange={e => setSearch(e.target.value)}
        />
        {dropdown && (
          <ul className="list-none absolute top-[42px] left-0 right-0 max-h-[200px] overflow-y-scroll shadow-md bg-white">
            {isLoading && <p className="text-center">Loading...</p>}
            {data?.map(user => (
              <li
                key={user.id}
                className="py-2 px-4 hover:bg-gray-500 hover:text-white transition-colors cursor-pointer"
                onClick={() => clickHandler(user.login)}
              >
                {user.login}
              </li>
            ))}
          </ul>
        )}
        <div className="container">
          {areReposLoading && <p className="text-center">Repos ara loading...</p>}
          {repos?.map(repo => (
            <RepoCard repo={repo} key={repo.id} />
          ))}
        </div>
      </div>
    </div>
  );
}
export default HomePage;
